@import url('https://fonts.googleapis.com/css2?family=Anek+Telugu:wght@200;300;500&family=Bebas+Neue&family=Comfortaa:wght@300;500;700&family=Oswald:wght@400;500&family=Raleway:wght@300;400;600&family=Roboto+Flex:opsz@8..144&family=Ubuntu+Condensed&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;


@layer components {
    .sidebar-icon{
        @apply relative cursor-pointer shadow-2xl flex items-center justify-center w-16 h-16 text-white m-1 mx-2 rounded-full hover:scale-110 bg-primary hover:bg-[#2e5f42] hover:border-2 transition-all duration-150 ease-in-out;
    }

    .sidebar-icon2{
        @apply relative cursor-pointer shadow-2xl flex items-center justify-center w-12 h-12 text-white rounded-full hover:scale-110 bg-primary hover:bg-[#2e5f42] hover:border-2 transition-all duration-150 ease-in-out;
    }

    .sidebar-text{
       @apply absolute text-[#1b7343] w-28 left-20 bg-white bg-opacity-90 text-center 
       justify-center rounded-lg p-3 transition-all duration-150 scale-0 origin-left;
    }

    .sidebar-text2{
        @apply absolute text-black lg:text-white top-14 w-24 bg-white lg:bg-black bg-opacity-90 text-center 
        justify-center rounded-lg p-1 transition-all duration-150 scale-0 origin-top;
     }

    .h1-custom{
        @apply font-Roboto text-6xl font-semibold tracking-wider;
    }

    .text-skills{
        @apply absolute w-32 h-32 md:w-64 md:h-64 flex place-content-center place-items-center text-white bg-black bg-opacity-80 rounded-full font-Roboto text-2xl scale-0 transition-all duration-150 origin-bottom;
    }

    .curveddiv{
        @apply bg-fourth text-white flex place-items-center place-content-center flex-col;
    }

    .curveddiv h1{
        @apply text-8xl font-Roboto;
    }

    .divbtn{
        @apply relative cursor-pointer shadow-2xl w-9 h-9 text-white bg-primary rounded-full hover:scale-110 transition-all duration-150 ease-in-out;
    }


    .imgsClass{
        @apply object-contain w-full h-full;
    }

    


}

